export const progressSend = () => {
  return (dispatch) => {
    dispatch({
      type: PROGRESS_SEND,
      payload: {
        data: true,
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
        error: false,
      },
    });
  };
};

export const finishSend = () => {
  return (dispatch) => {
    dispatch({
      type: FINISH_SEND,
      payload: {
        data: false,
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
        error: false,
      },
    });
  };
}
