export const GET_TABLE_TERIMA_MASAK = "GET_TABLE_TERIMA_MASAK";

export const getTableTerimaMasak = () => {
  return (dispatch) => {
    dispatch({
      type: GET_TABLE_TERIMA_MASAK,
      payload: {
        data: JSON.parse(localStorage.getItem("terimaMasak")),
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
        error: false,
      },
    });
  };
};
