import { AxiosMasterGet } from "../Axios";

export const GET_AREA_LIST = "GET_AREA_LIST";
export const EDIT_AREA = "EDIT_AREA";

export const getareaList = () => {
  return (dispatch) => {
    AxiosMasterGet("kecamatan/get/all")
      .then((res) =>
        dispatch({
          type: GET_AREA_LIST,
          payload: {
            data: res.data,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
            error: false,
          },
        })
      )
      .catch((err) =>
        dispatch({
          type: GET_AREA_LIST,
          payload: {
            data: false,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
            error: true,
          },
        })
      );
  };
};

export const editarea = (kodeArea, namaArea, kodeKota, Status) => {
  let data = {
    kodeArea: kodeArea,
    namaArea: namaArea,
    kodeKota: kodeKota,
    Status: Status,
  };
  console.log(data);
  return (dispatch) => {
    dispatch({
      type: EDIT_AREA,
      payload: {
        kodeArea: kodeArea,
        namaArea: namaArea,
        kodeKota: kodeKota,
        Status: Status,
      },
    });
  };
};
