import React, { Component } from "react";
import { connect } from "react-redux";
import { reset } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import {
  getListBarangPacking,
  getListBarangPackingM,
} from "../actions/PackingListAction";
import { berhasil, gagal, warning } from "../Alert";
import { AxiosMasterPost } from "../Axios";
import ModalMasterData from "../DataMaster/Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import FormTranskasiPenjualan from "./Component/FormTranskasiPenjualan";
import HeadPackingList from "./Component/HeadPackingList";

class PackingList extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.props.dispatch(getListBarangPacking());
    this.props.dispatch(getUUID());
  }
  handleSubmit(data) {
    let param = data.kodeBarang;
    let hasil = this.props.kodeBarang.filter(
      (data) => data.kode_jenis === param
    );
    var simpan = {
      netto_disc: parseFloat(data.nettoDiscGlobalExt).toFixed(3),
      gross: parseFloat(data.gross).toFixed(3), //berat + bungkus
      netto: parseFloat(data.netto).toFixed(3), //murni
      qty: 1,
      k: parseFloat(data.k),
      ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
      kadar_cetak: hasil[0].kadar_cetak,
      disc_persen: data.discGlobalPr,
      kadar_jual: hasil[0].kadar_jual, //harga%
      kadar_modal: hasil[0].kadar_beli, //harga%
      kode_jenis: hasil[0].kode_jenis, //kodebarang
      kode_barcode: "-",
      p: parseFloat(data.p),
      disc: data.discGlobalGr,
      pkg: parseFloat(data.pkg),
      bruto: parseFloat(data.bruto).toFixed(3), //berat
    };
    var array = JSON.parse(localStorage.getItem("packingList_temp")) || [];
    array.push(simpan);
    berhasil("Data Berhasil Ditambahkan");
    localStorage.setItem("packingList_temp", JSON.stringify(array));
    this.props.dispatch(reset("FormTransaksiPenjualan"));
    this.props.dispatch(getListBarangPacking());
  }

  handlePacking(data) {
    if (data.no_packing === undefined) {
      warning("Mohon Isi Nomor Packing");
      return false;
    } else if (data.tanggal === undefined) {
      warning("Mohon Isi Tanggal");
      return false;
    } else if (data.kode_customer === undefined) {
      warning("Mohon Pilih Kode Customer");
      return false;
    }
    let packingM = JSON.parse(localStorage.getItem("PackingM")) || [];
    let pkg = packingM
      .map((data) => parseFloat(data.pkg_m))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    let bruto = packingM
      .map((data) => parseFloat(data.bruto_m))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    let netto = packingM
      .map((data) => parseFloat(data.netto_m))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    let berat = packingM
      .map((data) => parseFloat(data.berat_p_m))
      .reduce((a, b) => a + b, 0);
    let beratS = packingM
      .map((data) => parseFloat(data.total_berat_p_s))
      .reduce((a, b) => a + b, 0);
    let total_item = packingM
      .map((data) => parseFloat(data.total_item))
      .reduce((a, b) => a + b, 0);
    let simpan = {
      no_packing: this.props.noFaktur,
      no_ext_packing: data.no_packing,
      no_po:
        localStorage.getItem("kode_PO_Packing") === null
          ? "-"
          : localStorage.getItem("kode_PO_Packing"),
      tanggal: data.tanggal,
      image: this.props.cameraURI,
      berat_p_l: data.berat_p_l,
      kode_customer: data.kode_customer,
      total_item: total_item,
      total_berat_p_s: beratS,
      total_berat_p_m: berat,
      pkg_l: pkg,
      bruto_l: bruto,
      netto_l: netto,
      packing_m: JSON.parse(localStorage.getItem("PackingM")),
    };
    AxiosMasterPost("packing-list/simpan", simpan)
      .then(() => berhasil("Berhasil Simpan Packing"))
      .then(() => localStorage.removeItem("PackingM"))
      .then(() => localStorage.removeItem("noFaktur"))
      .then(() => localStorage.removeItem("kode_PO_Packing"))
      .then(() => this.props.dispatch(getListBarangPackingM()))
      .then(() => this.props.dispatch(getUUID()))
      .then(() => this.props.dispatch(reset("HeadPackingList")))
      .then(() => this.props.dispatch(reset("AccordPOPAckingList")))
      .catch(() => gagal(`Gagal Simpan Packing`));
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <HeaderCard title="Packing List" />
            <div className="card-body">
              <HeadPackingList onSubmit={(data) => this.handlePacking(data)} />
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <FormTranskasiPenjualan
              onSubmit={(data) => this.handleSubmit(data)}
            />
          }
          title="Tambah Barang"
        />
      </div>
    );
  }
}

export default connect((state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    cameraURI: state.provinsi.cameraURI,
    kodeBarang: state.provinsi.kodeBarang,
  };
})(PackingList);
