import { AxiosMasterGet } from "../Axios";

export const GET_SUPPLIER_LIST = "GET_SUPPLIER_LIST";
export const EDIT_SUPPLIER = "EDIT_SUPPLIER";
export const GET_LOKASI_PINDAH = "GET_LOKASI_PINDAH";

export const getsupplierList = () => {
  return (dispatch) => {
    AxiosMasterGet("supplier/get/all")
      .then((res) =>
        dispatch({
          type: GET_SUPPLIER_LIST,
          payload: {
            data: res.data,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
            error: false,
          },
        })
      )
      .catch((err) =>
        dispatch({
          type: GET_SUPPLIER_LIST,
          payload: {
            data: false,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
            error: true,
          },
        })
      );
  };
};

export const getLokasiPindah = () => {
  return (dispatch) => {
    AxiosMasterGet("/lokasi/get/lokasi-sales")
      .then((res) =>
        dispatch({
          type: GET_LOKASI_PINDAH,
          payload: {
            data: res.data,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
            error: false,
          },
        })
      )
      .catch((err) =>
        dispatch({
          type: GET_SUPPLIER_LIST,
          payload: {
            data: false,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
            error: true,
          },
        })
      );
  };
};

export const editsupplier = (
  Alamat,
  Email,
  contactPerson,
  fax,
  handPhone,
  kodeExt,
  kodeKota,
  kodePos,
  kodeSupplier,
  namaSupplier,
  telepon,
  kodeProvinsi,
  kodeKecamatan,
  status_intern
) => {
  return (dispatch) => {
    dispatch({
      type: EDIT_SUPPLIER,
      payload: {
        Alamat: Alamat,
        Email: Email,
        contactPerson: contactPerson,
        fax: fax,
        handPhone: handPhone,
        kodeExt: kodeExt,
        kodeKota: kodeKota,
        kodePos: kodePos,
        kodeSupplier: kodeSupplier,
        namaSupplier: namaSupplier,
        telepon: telepon,
        kodeProvinsi: kodeProvinsi,
        kodeKecamatan: kodeKecamatan,
        status_intern: status_intern,
      },
    });
  };
};
