import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { AxiosMasterGet } from "../Axios";
import { renderField, renderFieldSelect } from "../renderField";

class CetakBarcodeModelHead extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list_barang: [],
    };
  }

  componentDidMount() {
    AxiosMasterGet("jenis/get/all").then((res) =>
      this.setState({
        list_barang: res.data,
      })
    );
  }
  pilihBarang(data) {
    var supp = data ? data : "DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("X");
    this.props.change("kode_jenis", result_explode[0]);
    this.props.change("kadar_cetak", result_explode[1]);
    this.props.change("kadar_jual", result_explode[2]);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-12">
            <h3>Barcode 1</h3>
            <hr />
            <div className="row">
              <div className="col-lg-6">
                <Field
                  name="kode_barang"
                  component={renderFieldSelect}
                  options={this.state.list_barang.map((list) => {
                    let value = {
                      value:
                        list.kode_jenis +
                        "X" +
                        list.kadar_cetak +
                        "X" +
                        list.kadar_jual,
                      name: list.nama_jenis,
                    };
                    return value;
                  })}
                  onChange={(data) => this.pilihBarang(data)}
                  type="text"
                  label="Kode Barang"
                  placeholder="Masukan Kode Barang"
                />
              </div>

              <div className="col-lg-6 d-none">
                <Field
                  name="kode_jenis"
                  component={renderField}
                  type="text"
                  label="Keterangan"
                  placeholder="Masukan Keterangan"
                  readOnly
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="kadar_cetak"
                  component={renderField}
                  type="text"
                  label="Kadar Cetak"
                  placeholder="Masukan Kadar Cetak"
                  readOnly
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="kadar_jual"
                  component={renderField}
                  type="text"
                  label="Kadar Jual"
                  placeholder="Masukan Kadar Jual"
                  readOnly
                />
              </div>
            </div>
          </div>
        </div>
        <div className="col-lg-12">
          <div className="text-right">
            <button className="btn btn-primary">
              <i className="fa fa-print mr-2"></i> Cetak
            </button>
          </div>
        </div>
      </form>
    );
  }
}

CetakBarcodeModelHead = reduxForm({
  form: "CetakBarcodeModelHead",
  enableReinitialize: true,
})(CetakBarcodeModelHead);
const selector = formValueSelector("CetakBarcodeModelHead");
export default connect((state) => {
  const { pkg, nw, pkg2, nw2 } = selector(state, "pkg", "nw", "pkg2", "nw2");
  return {
    gw: parseFloat(pkg || 0) + parseFloat(nw || 0),
    gw2: parseFloat(pkg2 || 0) + parseFloat(nw2 || 0),
  };
})(CetakBarcodeModelHead);
