import React, { Component } from "react";
import { connect } from "react-redux";
import ReactTooltip from "react-tooltip";
import { Field, formValueSelector, reduxForm, reset } from "redux-form";
import { getListBarangPacking } from "../../actions/PackingListAction";
import { getListPOPacking } from "../../actions/validasiPOAction";
import { berhasil, warning } from "../../Alert";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";
import { renderField } from "../../renderField";

class AccordPOPAckingList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar\nCetak(%)",
        },
        {
          dataField: "pkg",
          text: "Bungkus\n(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
        },
        {
          dataField: "kadar_jual",
          text: "Harga\n(%)",
        },
        {
          dataField: "netto",
          text: "Murni\n(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc_persen",
          text: "%",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc",
          text: "Disc\n(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "netto_disc",
          text: "Netto\nDisc(Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  type="button"
                  data-tip
                  data-for="hapus"
                  className="btn btn-success mr-2"
                  onClick={() => this.pilihPO(row)}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Pilih</span>
                  </ReactTooltip>
                  <i className="fa fa-plus"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
    };
  }

  pilihPO(simpan) {
    var array = JSON.parse(localStorage.getItem("packingList_temp")) || [];
    var hasil = array.findIndex(
      (data) => data.kode_jenis === simpan.kode_jenis
    );
    if (hasil === -1) {
      delete simpan.kode_kategori;
      delete simpan.nama_jenis;
      delete simpan._id;
      array.push(simpan);
      berhasil("Data Berhasil Ditambahkan");
      localStorage.setItem("packingList_temp", JSON.stringify(array));
      this.props.dispatch(reset("FormTransaksiPenjualan"));
      this.props.dispatch(getListBarangPacking());
    } else {
      warning("Barang Sudah ada di List Barang PO");
    }
  }
  getPO() {
    if (this.props.kode_customer === undefined) {
      warning("Silahkan Pilih Kode Customer Terlebih dahulu");
      return false;
    } else {
      localStorage.setItem("kode_PO_Packing", this.props.kode_po);
      this.props.dispatch(
        getListPOPacking(this.props.kode_po, this.props.kode_customer)
      );
    }
  }
  render() {
    return (
      <div className="col-lg-12 mt-2">
        <div id="accordion" className="custom-accordion mb-4">
          <div className="card mb-0 bg-primary">
            <div className="card-header" id="headingOne">
              <div className="row">
                <div className="col-lg-12">
                  <h5 className="m-0">
                    <a
                      className="custom-accordion-title d-block pt-2 pb-2 text-white"
                      data-toggle="collapse"
                      href="#collapsePO"
                      aria-expanded="true"
                      aria-controls="collapsePO"
                    >
                      Data PO
                    </a>
                  </h5>
                </div>
              </div>
            </div>
            <div
              id="collapsePO"
              className="collapse"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div className="card-body bg-white">
                <form onSubmit={this.props.handleSubmit}>
                  <div className="row">
                    <div className="col-lg-4">
                      <Field
                        name="kode_po"
                        component={renderField}
                        type="text"
                        label="Kode PO"
                        placeholder="Masukan Kode PO"
                      />
                    </div>
                    <div className="col-lg-2">
                      <div className="form-group">
                        <label htmlFor="">&nbsp;</label>
                        <button
                          className="btn btn-primary form-control"
                          type="button"
                          onClick={() => this.getPO()}
                        >
                          Cari PO
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12">
                      <p>
                        *Mohon isi Kode Customer Terlebih dahulu sebelum mencari
                        PO
                      </p>
                    </div>
                    <div className="col-lg-12">
                      <GlobalTabel
                        keyField="kodeJenis"
                        data={this.props.listPOPacking || []}
                        columns={this.state.columns}
                        textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
                      />
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AccordPOPAckingList = reduxForm({
  form: "AccordPOPAckingList",
  enableReinitialize: true,
})(AccordPOPAckingList);
const selector = formValueSelector("AccordPOPAckingList");
export default connect((state) => {
  const { kode_po } = selector(
    state,
    "kode_po",
    "kadarTransaksi",
    "pkg",
    "discGlobalPr"
  );
  return {
    listPOPacking: state.provinsi.listPOPacking.detail_barang,
    kode_po: kode_po,
  };
})(AccordPOPAckingList);
