import { HIDE_MODAL, SHOW_MODAL } from "../actions/ModalAction";

const initialState = {
  show: false,
};

const modal = (state = initialState, action) => {
  switch (action.type) {
    case SHOW_MODAL:
      return {
        ...state,
        show: action.payload.data,
      };
    case HIDE_MODAL:
      return {
        ...state,
        show: action.payload.data,
      };
    default:
      return {
        ...state,
      };
  }
};

export default modal;
