export const GET_CAMERA_URI = "GET_CAMERA_URI";

export const setCameraURI = (dataUri) => {
  return (dispatch) => {
    dispatch({
      type: GET_CAMERA_URI,
      payload: {
        data: dataUri,
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
        error: false,
      },
    });
  };
};
