export const GET_DATA_PENGAMBILAN_BARCODE = "GET_DATA_PENGAMBILAN_BARCODE";

export const getListDataPengambilanBarcode = () => {
  return (dispatch) => {
    dispatch({
      type: GET_DATA_PENGAMBILAN_BARCODE,
      payload: {
        data: JSON.parse(localStorage.getItem("pengambilanBarcode_temp")),
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
        error: false,
      },
    });
  };
};
