import React, { Component } from "react";
import { connect } from "react-redux";
import ReactTooltip from "react-tooltip";
import { Field, formValueSelector, reduxForm } from "redux-form";
import Swal from "sweetalert2";
import { getcustomerList } from "../../actions/customerAction";
import { showModal } from "../../actions/ModalAction";
import {
  getListBarangPacking,
  getListBarangPackingM,
  getListBarangPackingS,
} from "../../actions/PackingListAction";
import { berhasil } from "../../Alert";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";
import { renderField, renderFieldSelect } from "../../renderField";
import AccordPOPAckingList from "./AccordPOPackingList";
import uuid from "react-uuid";
import Camera from "../../Camera";
import { PlaceHolder } from "../../helper";
import { setCameraURI } from "../../actions/cameraAction";

class HeadPackingList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tablePO: true,
      image: "",
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar\nCetak(%)",
        },
        {
          dataField: "pkg",
          text: "Bungkus\n(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
        },
        {
          dataField: "kadar_jual",
          text: "Harga\n(%)",
        },
        {
          dataField: "netto",
          text: "Murni\n(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc_persen",
          text: "%",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc",
          text: "Disc\n(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "netto_disc",
          text: "Netto\nDisc(Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  type="button"
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => this.deleteRow(rowIndex, "packingList_temp")}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
      columnsPackingS: [
        {
          dataField: "total_item",
          text: "Total Qty",
        },
        {
          dataField: "berat_p_s",
          text: "Berat Packing S",
        },
        {
          dataField: "pkg_s",
          text: "Total Packing Barang",
        },
        {
          dataField: "bruto_s",
          text: "TOtal Bruto",
        },
        {
          dataField: "netto_s",
          text: "Total Netto",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  type="button"
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => this.deleteRow(rowIndex, "PackingS")}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
      columnsPackingM: [
        {
          dataField: "total_item",
          text: "Total Qty",
        },
        {
          dataField: "berat_p_m",
          text: "Berat Packing M",
        },
        {
          dataField: "pkg_m",
          text: "Total Packing Barang",
        },
        {
          dataField: "bruto_m",
          text: "TOtal Bruto",
        },
        {
          dataField: "netto_m",
          text: "Total Netto",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  type="button"
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => this.deleteRow(rowIndex, "PackingM")}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
    };
  }

  deleteRow(rowIndex, tabel) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Menghapus Data Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        const array = JSON.parse(localStorage.getItem(tabel)) || [];
        array.splice(rowIndex, 1);
        localStorage.setItem(tabel, JSON.stringify(array));
        berhasil("Berhasil Hapus Data");
        this.props.dispatch(getcustomerList());
        this.props.dispatch(getListBarangPackingS());
        this.props.dispatch(getListBarangPackingM());
        this.props.dispatch(getListBarangPacking());
      }
    });
  }

  componentDidMount() {
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getListBarangPackingS());
    this.props.dispatch(getListBarangPackingM());
  }

  handleBarangPO(data) {
    console.log(data);
  }
  handlePackingM() {
    let data = JSON.parse(localStorage.getItem("PackingS")) || [];
    let sebelum = JSON.parse(localStorage.getItem("PackingM")) || [];
    let pkg = data
      .map((data) => parseFloat(data.pkg_s))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    let bruto = data
      .map((data) => parseFloat(data.bruto_s))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    let netto = data
      .map((data) => parseFloat(data.netto_s))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    let berat = data
      .map((data) => parseFloat(data.berat_p_s))
      .reduce((a, b) => a + b, 0);
    let total_item = data
      .map((data) => parseFloat(data.total_item))
      .reduce((a, b) => a + b, 0);
    let simpan = {
      no_packing_m: uuid(),
      total_item: total_item,
      berat_p_m: this.props.berat_p_m,
      total_berat_p_s: berat,
      pkg_m: pkg,
      bruto_m: bruto,
      netto_m: netto,
      packing_s: data,
    };
    sebelum.push(simpan);
    localStorage.setItem("PackingM", JSON.stringify(sebelum));
    berhasil("Berhasil Menambahkan ke Packing M");
    localStorage.removeItem("PackingS");
    this.props.dispatch(getListBarangPackingS());
    this.props.dispatch(getListBarangPackingM());
  }

  handlePackingS() {
    let data = JSON.parse(localStorage.getItem("packingList_temp")) || [];
    let sebelum = JSON.parse(localStorage.getItem("PackingS")) || [];
    let pkg = data
      .map((data) => parseFloat(data.pkg))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    let bruto = data
      .map((data) => parseFloat(data.bruto))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    let netto = data
      .map((data) => parseFloat(data.netto))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    let simpan = {
      no_packing_s: uuid(),
      total_item: data.length,
      berat_p_s: this.props.berat_p_s,
      pkg_s: pkg,
      bruto_s: bruto,
      netto_s: netto,
      detail_barang: data,
    };
    sebelum.push(simpan);
    localStorage.setItem("PackingS", JSON.stringify(sebelum));
    berhasil("Berhaisl Menambahkan ke Packing S");
    localStorage.removeItem("packingList_temp");
    this.props.dispatch(getListBarangPacking());
    this.props.dispatch(getListBarangPackingS());
  }

  handleUpload = (e) => {
    const reader = new FileReader();
    const file = e.target.files[0];
    reader.onload = this.handleReaderLoaded;
    reader.readAsDataURL(file);
    console.log("reader", reader);
    console.log("file", file);
    if (reader !== undefined && file !== undefined) {
      reader.onloadend = () => {
        this.props.dispatch(setCameraURI(reader.result));
      };
    }
  };

  handleReaderLoaded = (readerEvt) => {
    let binaryString = readerEvt.target.result;
    this.setState({
      imageBase64: btoa(binaryString),
    });
  };

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="col-lg-12">
          <div className="row">
            <div className="col-lg-3">
              <Field
                name="tanggal"
                component={renderField}
                type="date"
                label="Tanggal"
                placeholder="Masukan Tanggal"
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="no_packing"
                component={renderField}
                type="text"
                label="Nomor Packing"
                placeholder="Masukan Nomor Packing"
              />
            </div>
            <div className="col-lg-4">
              <Field
                name="kode_customer"
                component={renderFieldSelect}
                options={this.props.customer.map((data) => {
                  let hasil = {
                    value: data.kode_customer,
                    name: `${data.nama_customer} - ${data.nama_toko}`,
                  };
                  return hasil;
                })}
                type="text"
                label="Kode Customer"
                placeholder="Masukan Kode Customer"
              />
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={() => this.props.dispatch(showModal())}
                >
                  Tambah Data Barang
                </button>
              </div>
            </div>

            <AccordPOPAckingList
              onSubmit={(data) => this.handleBarangPO(data)}
              kode_customer={this.props.kode_customer}
            />
            <div className="col-lg-12">
              <GlobalTabel
                keyField="kodeJenis"
                data={this.props.packingList || []}
                columns={this.state.columns}
                textEmpty="Data Untuk Dipacking Kosong, Silahkan Tambahkan dengan menekan tombol biru diatas"
              />
            </div>
            <div className="col-lg-8">
              <div style={{ float: "right" }}>
                <div className="col-lg-12">
                  <Field
                    name="berat_p_s"
                    component={renderField}
                    type="number"
                    label="Berat Packing S"
                    placeholder="Masukan Berat Packing S"
                  />
                </div>
              </div>
            </div>
            <div className="col-lg-4">
              <div className="text-right form-group">
                <label htmlFor="">&nbsp;</label>
                <button
                  type="button"
                  className="btn btn-primary form-control"
                  onClick={() => this.handlePackingS()}
                >
                  Simpan Packing S
                </button>
              </div>
            </div>
            <div className="col-lg-12">
              <hr />
            </div>
            <div className="col-lg-12">
              <GlobalTabel
                keyField="kodeJenis"
                data={this.props.listPackingS || []}
                columns={this.state.columnsPackingS}
                textEmpty="Data Packing S Kosong"
              />
            </div>
            <div className="col-lg-8">
              <div style={{ float: "right" }}>
                <div className="col-lg-12">
                  <Field
                    name="berat_p_m"
                    component={renderField}
                    type="number"
                    label="Berat Packing M"
                    placeholder="Masukan Berat Packing M"
                  />
                </div>
              </div>
            </div>
            <div className="col-lg-4">
              <div className="text-right form-group">
                <label htmlFor="">&nbsp;</label>
                <button
                  type="button"
                  className="btn btn-primary form-control"
                  onClick={() => this.handlePackingM()}
                >
                  Simpan Packing M
                </button>
              </div>
            </div>
            <div className="col-lg-12">
              <hr />
            </div>
            <div className="col-lg-12">
              <GlobalTabel
                keyField="kodeJenis"
                data={this.props.listPackingM || []}
                columns={this.state.columnsPackingM}
                textEmpty="Data Packing S Kosong"
              />
            </div>
            <div className="col-lg-5">
              <div className="col-lg-12" style={{ float: "right" }}>
                <img
                  src={
                    this.props.cameraURI === ""
                      ? PlaceHolder
                      : this.props.cameraURI
                  }
                  alt="Gambar Packing"
                  width="200px"
                  height="200px"
                />
              </div>
              <div className="col-lg-12" style={{ float: "right" }}>
                <button
                  className="btn btn-primary mt-2 mb-2"
                  type="button"
                  data-toggle="modal"
                  data-target="#cameraModal"
                >
                  Buka Kamera
                </button>
              </div>
              <div class="input-group mt-2">
                <div class="input-group-prepend">
                  <button
                    class="btn btn-danger"
                    type="button"
                    onClick={() => this.props.dispatch(setCameraURI(""))}
                  >
                    Hapus
                  </button>
                </div>
                <div class="custom-file">
                  <input
                    className="custom-file-input"
                    type="file"
                    name="avatar"
                    id="file"
                    accept=".jpef, .png, .jpg"
                    onChange={(data) => this.handleUpload(data)}
                    src={this.state.image}
                  />
                  <label class="custom-file-label" for="inputGroupFile03">
                    Choose file
                  </label>
                </div>
              </div>
            </div>
            <div className="col-lg-3">
              <div style={{ float: "right" }}>
                <div className="col-lg-12">
                  <Field
                    name="berat_p_l"
                    component={renderField}
                    type="number"
                    label="Berat Packing L"
                    placeholder="Masukan Berat Packing L"
                  />
                </div>
              </div>
            </div>
            <div className="col-lg-4">
              <div className="text-right form-group">
                <label htmlFor="">&nbsp;</label>
                <button className="btn btn-primary form-control">
                  Simpan Packing L
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="cameraModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data Timbangan
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Camera kodeCustomer={this.state.kodeCustomer} />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

HeadPackingList = reduxForm({
  form: "HeadPackingList",
})(HeadPackingList);
const selector = formValueSelector("HeadPackingList");
export default connect((state) => {
  const { berat_p_s, berat_p_m, kode_customer } = selector(
    state,
    "berat_p_s",
    "berat_p_m",
    "kode_customer"
  );
  return {
    customer: state.provinsi.customer,
    packingList: state.provinsi.packingList,
    berat_p_s: parseFloat(berat_p_s || 0),
    berat_p_m: parseFloat(berat_p_m || 0),
    listPackingS: state.provinsi.listPackingS,
    listPackingM: state.provinsi.listPackingM,
    kode_customer: kode_customer,
    initialValues: {
      berat_p_s: 0,
      berat_p_m: 0,
      berat_p_l: 0,
    },
    cameraURI: state.provinsi.cameraURI,
  };
})(HeadPackingList);
