import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import ReactTooltip from "react-tooltip";
import { Field, formValueSelector, reduxForm, reset } from "redux-form";
import { getKelompokHarga } from "../actions/kelompok_harga";
import { getListBarang } from "../actions/kodeBarangAction";
import { berhasil, gagal } from "../Alert";
import { AxiosMasterPost, AxiosMasterPut } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import HeaderCard from "../HeaderCard";
import { renderField, renderFieldSelect } from "../renderField";

class KelompokHarga extends Component {
  constructor(props) {
    super(props);
    this.state = {
      edit: false,
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
          headerStyle: () => {
            return { width: "30%" };
          },
        },
        {
          dataField: "level2",
          text: "Nama Model",
          headerStyle: () => {
            return { width: "20%" };
          },
        },
        {
          dataField: "level3",
          text: "Kode Kategori",
          headerStyle: () => {
            return { width: "30%" };
          },
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-warning"
                  onClick={() => this.edit(row)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-edit mr-1"></i>
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  Edit
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  edit(data) {
    this.props.change("kode_jenis", data.kode_jenis);
    this.props.change("level_2", data.level2);
    this.props.change("level_3", data.level3);
    this.setKadarJual(data.kode_jenis);
    this.setState({
      edit: true,
    });
  }
  componentDidMount() {
    this.props.dispatch(getKelompokHarga());
    this.props.dispatch(getListBarang());
  }
  simpanKelompok() {
    let data = {
      kode_jenis: this.props.kode_jenis,
      level2: this.props.level_2,
      level3: this.props.level_3,
    };
    let dataEdit = {
      level2: this.props.level_2,
      level3: this.props.level_3,
    };
    !this.state.edit
      ? AxiosMasterPost("kelompok-harga/add", data)
          .then(() => berhasil("Berhasil Disimpan"))
          .then(() => this.props.dispatch(reset("KelompokHarga")))
          .then(() => this.props.dispatch(getKelompokHarga()))
          .catch((err) =>
            gagal(`Gagal Menyimpan Data, Detail : ${err.response.data}`)
          )
      : AxiosMasterPut(`kelompok-harga/edit/${this.props.kode_jenis}`, dataEdit)
          .then(() => berhasil("Berhasil Disimpan"))
          .then(() => this.props.dispatch(reset("KelompokHarga")))
          .then(() => this.props.dispatch(getKelompokHarga()))
          .catch((err) =>
            gagal(`Gagal Menyimpan Data, Detail : ${err.response.data}`)
          );
  }
  setKadarJual(data) {
    let param = data;
    let kadarJual = this.props.kodeBarang.filter(
      (data) => data.kode_jenis === param
    );
    this.props.change("kadar_jual", kadarJual[0].kadar_jual);
    this.setState({
      edit: false,
    });
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <ol className="breadcrumb mb-2">
          <Link to="/KlasifikasiBarang">
            <button type="button" className="btn btn-secondary ">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          <div className="col-lg-4">
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12">
                  <h2>Tambah Kelompok Harga</h2>
                </div>
                <div className="col-lg-12">
                  <hr />
                </div>
                <div className="col-lg-12">
                  <Field
                    name="kode_jenis"
                    component={renderFieldSelect}
                    options={this.props.kodeBarang.map((data) => {
                      let hasil = {
                        value: data.kode_jenis,
                        name: data.nama_jenis,
                      };
                      return hasil;
                    })}
                    type="text"
                    label="Kode Jenis"
                    placeholder="Masukan Kode Jenis"
                    onChange={(data) => this.setKadarJual(data)}
                  />
                </div>
                <div className="col-lg-12">
                  <Field
                    name="kadar_jual"
                    component={renderField}
                    type="number"
                    label="Harga Kadar Jual"
                    placeholder="Masukan Harga Kadar Jual"
                    readOnly
                  />
                </div>
                <div className="col-lg-12">
                  <Field
                    name="level_2"
                    component={renderField}
                    type="number"
                    label="Harga Level 2"
                    placeholder="Masukan Harga Level 2"
                  />
                </div>
                <div className="col-lg-12">
                  <Field
                    name="level_3"
                    component={renderField}
                    type="number"
                    label="Harga Level 3"
                    placeholder="Masukan Harga Level 3"
                  />
                </div>
                <div className="col-lg-12 mt-2">
                  <button
                    className="btn btn-primary btn-block"
                    onClick={() => this.simpanKelompok()}
                  >
                    SIMPAN
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-8">
            <div className="card">
              <HeaderCard title="Kelompok Harga" />
              <div className="card-body">
                <GlobalTabel
                  keyField="id"
                  data={this.props.KelompokHarga}
                  columns={this.state.columns}
                  search
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

KelompokHarga = reduxForm({
  form: "KelompokHarga",
})(KelompokHarga);
const selector = formValueSelector("KelompokHarga");
export default connect((state) => {
  const { kode_jenis, level_2, level_3 } = selector(
    state,
    "level_2",
    "level_3",
    "kode_jenis"
  );
  return {
    KelompokHarga: state.provinsi.kelompokHarga,
    kodeBarang: state.provinsi.kodeBarang,
    kode_jenis: kode_jenis,
    level_2: level_2,
    level_3: level_3,
  };
})(KelompokHarga);
