import { AxiosMasterGet } from "../Axios";

export const GET_DATA_CLOSE_PENGAMBILAN_BARCODE =
  "GET_DATA_CLOSE_PENGAMBILAN_BARCODE";
export const GET_LIST_CLOSE_BARCODE = "GET_LIST_CLOSE_BARCODE";
export const GET_CLOSE_PENGAMBILAN_BARCODE_SELECTED =
  "GET_CLOSE_PENGAMBILAN_BARCODE_SELECTED";

export const getListClosePengambilanBarcode = () => {
  return (dispatch) => {
    dispatch({
      type: GET_DATA_CLOSE_PENGAMBILAN_BARCODE,
      payload: {
        data: JSON.parse(localStorage.getItem("closePengambilanBarcode")),
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
        error: false,
      },
    });
  };
};

export const getClosePengambilanBarcode = () => {
  return (dispatch) => {
    AxiosMasterGet(`pengambilan-barcode/get-open`)
      .then((res) =>
        dispatch({
          type: GET_LIST_CLOSE_BARCODE,
          payload: {
            data: res.data,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
            error: false,
          },
        })
      )
      .catch(() =>
        dispatch({
          type: GET_LIST_CLOSE_BARCODE,
          payload: {
            data: false,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
            error: true,
          },
        })
      );
  };
};

export const getClosePengambilanBarcodeSelected = (row, no_barcode) => {
  return (dispatch) => {
    dispatch({
      type: GET_CLOSE_PENGAMBILAN_BARCODE_SELECTED,
      payload: {
        data: row,
        no_barcode: no_barcode,
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
        error: true,
      },
    });
  };
};
