  HIDE_ALERT_LOGIN,
  SET_ALERT_LOGIN,
  SET_DASHBOARD,
  SET_DEFAULT,
  SET_FICO,
  SET_JUALBELILM,
  SET_LAPORAN,
  SET_LOGIN,
  SET_MASTER,
  SET_STOKING,
  SET_SUPERVISORY,
  SET_TITLE,
  SET_TOTALAN,
  SET_TRANSAKSI,
