import React, { Component } from "react";
import HeaderCard from "../HeaderCard";
import CetakBarcodeHead from "./CetakBarcodeHead";
import { getListBarang } from "../actions/kodeBarangAction";
import { connect } from "react-redux";

class CetakBarcode extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
  }

  paddingNumberGW(str) {
    var pad = "00000000";
    var ans = pad.substring(0, pad.length - str.length) + str;
    return ans.replace(".", "");
  }
  paddingNumberNW(str) {
    var pad = "000000";
    var ans = pad.substring(0, pad.length - str.length) + str;
    return ans.replace(".", "");
  }
  handleSubmit(data) {
    let feedback = "";
    // let feedback = `AEX0ARA3H001V001CS2#E4APM0PO3+00YE0IG1PH0A102000671Z##                            APSWKesther grosir LB`;
    // feedback += `%2H0637V0187L0102P02S${data.kode_barang} - ${data.sub_kategori}`;
    // // BARCODE
    // feedback += `%2H0622V0158BG01067>H${
    //   data.kode_barang
    // }X>C${this.paddingNumberGW(
    //   parseFloat(data.gw).toFixed(2)
    // )}${this.paddingNumberNW(parseFloat(data.nw).toFixed(2))}`;
    // feedback += `%2H0622V0087L0101P02S${data.kode_barang}X>C${this.paddingNumberGW(
    //   parseFloat(data.gw).toFixed(2)
    // )}${this.paddingNumberNW(parseFloat(data.nw).toFixed(2))}`;
    // // BARCODE
    // feedback += `%2H0650V0058L0101P02S${"GW"}%2H0622V0065L0102P02S${parseFloat(
    //   data.gw || 0
    // ).toFixed(2)}`;
    // feedback += `%2H0650V0028L0101P02S${"NW"}%2H0622V0038L0102P02S${parseFloat(
    //   data.nw || 0
    // ).toFixed(2)}`;
    // feedback += `%2H0535V0058L0101P02S${"ONG"}%2H0497V0068L0102P02SRp. ${
    //   parseFloat(data.ongkos || 0).toLocaleString("id-ID") || 0
    // }`;
    // feedback += `%2H0515V0028L0101P02S${"T"}%2H0497V0038L0102P02S${parseFloat(
    //   data.tukaran || 0
    // ).toFixed(2)}%`;
    feedback += `<xpml><page quantity='0' pitch='35.0 mm'></xpml>I8,A\n`;
    feedback += `ZN\n`;
    feedback += `q504\n`;
    feedback += `O\n`;
    feedback += `JF\n`;
    feedback += `KIZZQ0\n`;
    feedback += `KI9+0.0\n`;
    feedback += `ZT\n`;
    feedback += `Q280,B25\n`;
    feedback += `Arglabel 350 31\n`;
    feedback += `exit\n`;
    feedback += `KI80\n`;
    feedback += `<xpml></page></xpml><xpml><page quantity='1' pitch='35.0 mm'></xpml>N\n`;
    feedback += `A400,275,2,2,1,2,N,"${data.kode_barang} - ${
      data.sub_kategori || ""
    }"\n`;
    // BARCODE
    feedback += `B350,238,2,1,1,10,73,N,"${
      data.kode_barang
    }X${this.paddingNumberGW(
      parseFloat(data.gw).toFixed(2)
    )}${this.paddingNumberNW(parseFloat(data.nw).toFixed(2))}"\n`;
    // BARCODE
    feedback += `A400,155,2,2,1,2,N,"${
      data.kode_barang
    }X>C${this.paddingNumberGW(
      parseFloat(data.gw).toFixed(2)
    )}${this.paddingNumberNW(parseFloat(data.nw).toFixed(2))}"\n`;
    feedback += `A400,107,2,2,1,2,N,"GW"\n`;
    feedback += `A400,62,2,2,1,2,N,"NW"\n`;
    feedback += `A365,107,2,2,1,2,N,"${parseFloat(data.gw || 0).toFixed(2)}"\n`;
    feedback += `A365,62,2,2,1,2,N,"${parseFloat(data.nw || 0).toFixed(2)}"\n`;
    feedback += `A250,107,2,2,1,2,N,"ONG"\n`;
    feedback += `A250,62,2,2,1,2,N,"T"\n`;
    feedback += `A205,107,2,2,1,2,N,"${
      parseFloat(data.ongkos || 0).toLocaleString("id-ID") || 0
    }"\n`;
    feedback += `A205,62,2,2,1,2,N,"${parseFloat(data.tukaran || 0).toFixed(
      2
    )}%"\n`;
    feedback += `P1\n`;
    feedback += `<xpml></page></xpml><xpml><end/></xpml>`;
    document.getElementById("myInput").value = feedback;
    const element = document.createElement("a");
    const file = new Blob([document.getElementById("myInput").value], {
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);
    element.download = "autoprint_barcode.txt";
    document.body.appendChild(element);
    element.click();
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Cetak Barcode" />
          <div className="card-body">
            <div className="row">
              <div className="col-lg-12">
                {/* BARCODE 1 */}
                <CetakBarcodeHead
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
              <div className="d-none">
                <textarea id="myInput"></textarea>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect()(CetakBarcode);
