import React from "react";
import CameraPhoto, { FACING_MODES } from "jslib-html5-camera-photo";
import { PlaceHolder } from "./helper";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import { setCameraURI } from "./actions/cameraAction";

class Camera extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.cameraPhoto = null;
    this.videoRef = React.createRef();
    this.state = {
      dataUri: PlaceHolder,
    };
  }

  componentDidMount() {
    this.cameraPhoto = new CameraPhoto(this.videoRef.current);
    setTimeout(() => {
      this.cameraPhoto.startCamera(FACING_MODES.ENVIRONMENT, {
        width: 720,
        height: 720,
      });
    }, 1000);
  }

  componentWillUnmount() {
    this.cameraPhoto
      .stopCamera()
      .then(() => {
        console.log("Camera stoped!");
      })
      .catch((error) => {
        console.log("No camera to stop!:", error);
      });
  }
  takePhoto() {
    const config = {
      sizeFactor: 1,
      imageCompression: 0.1,
      isImageMirror: false,
    };

    let dataUri = this.cameraPhoto.getDataUri(config);
    this.setState({ dataUri });
    this.props.dispatch(setCameraURI(dataUri));
  }

  stopCamera() {
    this.cameraPhoto
      .stopCamera()
      .then(() => {
        console.log("Camera stoped!");
      })
      .catch((error) => {
        console.log("No camera to stop!:", error);
      });
  }

  render() {
    return (
      <div className="col-lg-12">
        <div className="row">
          <div className="col-lg-6">
            <video
              ref={this.videoRef}
              autoPlay="true"
              width="100%"
              height="95%"
            />
          </div>
          <div className="col-lg-6">
            <img
              alt="imgCamera"
              src={this.state.dataUri}
              width="95%"
              height="95%"
            />
          </div>
          <div className="col-lg-12 mt-3">
            <div className="row">
              <div className="col-lg-3">
                <div className="text-center">
                  <button
                    className="btn btn-primary btn-block"
                    type="button"
                    onClick={() => this.takePhoto()}
                  >
                    Ambil Gambar
                  </button>
                </div>
              </div>
              <div className="col-lg-3">
                <div className="text-center">
                  <button
                    className="btn btn-danger btn-block"
                    type="button"
                    onClick={() =>
                      this.setState({
                        dataUri: PlaceHolder,
                      })
                    }
                  >
                    Reset Gambar
                  </button>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    type="button"
                    data-dismiss="modal"
                  >
                    Simpan Gambar
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Camera = reduxForm({
  form: "Camera",
})(Camera);
export default connect()(Camera);
