import { AxiosMasterGet } from "../Axios";

export const GET_DATA_REKAP_BON_CUSTOMER = "GET_DATA_REKAP_BON_CUSTOMER";
export const GET_DATA_REKAP_BON = "GET_DATA_REKAP_BON";

export const getListRekapBon = (tglFrom, tglTo, namaCustomer, kodeCustomer) => {
  return (dispatch) => {
    AxiosMasterGet("cst/dc/pjl/heads", {
      params: {
        tglFrom: tglFrom,
        tglTo: tglTo,
        namaCustomer: namaCustomer,
        kodeCustomer: kodeCustomer,
        statusValid: "VALID",
      },
    })
      .then((res) =>
        dispatch({
          type: GET_DATA_REKAP_BON_CUSTOMER,
          payload: {
            data: res.data,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
            error: false,
          },
        })
      )
      .catch((err) =>
        dispatch({
          type: GET_DATA_REKAP_BON_CUSTOMER,
          payload: {
            data: false,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
            error: true,
          },
        })
      );
  };
};

export const getListRekapBonSelected = () => {
  return (dispatch) => {
    dispatch({
      type: GET_DATA_REKAP_BON,
      payload: {
        data: JSON.parse(localStorage.getItem("FakturRekapBon")),
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
        error: false,
      },
    });
  };
};
