import { AxiosMasterGet } from "../Axios";

export const GET_ROSOK_LIST = "GET_ROSOK_LIST";
export const EDIT_ROSOK = "EDIT_ROSOK";

export const getListRosok = () => {
  return (dispatch) => {
    AxiosMasterGet("jenis/get/all")
      .then((res) =>
        dispatch({
          type: GET_ROSOK_LIST,
          payload: {
            data: res.data.filter(
              (list) =>
                list.kode_kelompok === "CIOK" ||
                list.kode_kelompok === "RONGSOK"
            ),
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
            error: false,
          },
        })
      )
      .catch((err) =>
        dispatch({
          type: GET_ROSOK_LIST,
          payload: {
            data: false,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
            error: true,
          },
        })
      );
  };
};

export const editRosok = (
  kodeJenis,
  kodeKategori,
  namaJenis,
  kadarBeli,
  kadarJual,
  kadarCetak,
  image,
  kodeIntern,
  kodeBarcode
) => {
  return (dispatch) => {
    dispatch({
      type: EDIT_ROSOK,
      payload: {
        kodeJenis: kodeJenis,
        kodeKategori: kodeKategori,
        namaJenis: namaJenis,
        kadarBeli: kadarBeli,
        kadarJual: kadarJual,
        kadarCetak: kadarCetak,
        image: image,
        kodeIntern: kodeIntern,
        kodeBarcode: kodeBarcode,
      },
    });
  };
};
