const checkMenu = (data1) => {
  let akses = localStorage.getItem("level") || "OWN";
  if (akses === "OWN") {
    return true;
  } else {
    let check = localStorage.getItem("menu") || "kosong";
    if (check !== "kosong") {
      let data = JSON.parse(localStorage.getItem("menu")) || [];
      return data.find((list) => list.menu_id === data1);
    }
  }
};
